import * as React from 'react'
import { useUID } from 'react-uid'
import { Close } from '@datapunt/asc-assets'
import FormLabelStyle from '../FormLabelStyle'
import TextFieldStyle from './TextFieldStyle'
import Input, { InputProps } from '../Input'
import Button from '../Button'
import Icon from '../Icon'

export interface TextFieldProps extends InputProps {
  label?: string
  keepFocus?: boolean
  srOnly: boolean
  onClear?: Function
}

const TextField = ({
  label,
  srOnly,
  onClear,
  value,
  keepFocus,
  blurOnEscape,
  focusOnRender,
  ...otherProps
}: TextFieldProps) => {
  const uid = useUID()
  return (
    <TextFieldStyle>
      <FormLabelStyle htmlFor={uid} srOnly={srOnly}>
        {label}
      </FormLabelStyle>
      <Input
        {...{ keepFocus, value, blurOnEscape, focusOnRender }}
        {...otherProps}
        id={uid}
      />
      {onClear && value && (
        <Button
          size={30}
          variant="blank"
          type="button"
          aria-label="Close"
          onClick={() => onClear()}
        >
          <Icon>
            <Close />
          </Icon>
        </Button>
      )}
    </TextFieldStyle>
  )
}

TextField.defaultProps = {
  srOnly: false,
  canClear: true,
}

export default TextField
try {
    // @ts-ignore
    TextField.displayName = "TextField";
    // @ts-ignore
    TextField.__docgenInfo = { "description": "", "displayName": "TextField", "props": { "label": { "defaultValue": null, "description": "", "name": "label", "required": false, "type": { "name": "string" } }, "keepFocus": { "defaultValue": null, "description": "", "name": "keepFocus", "required": false, "type": { "name": "boolean" } }, "srOnly": { "defaultValue": { value: false }, "description": "", "name": "srOnly", "required": false, "type": { "name": "boolean" } }, "onClear": { "defaultValue": null, "description": "", "name": "onClear", "required": false, "type": { "name": "Function" } } } };
    // @ts-ignore
    if (typeof STORYBOOK_REACT_CLASSES !== "undefined")
        // @ts-ignore
        STORYBOOK_REACT_CLASSES["packages/asc-ui/src/components/TextField/TextField.tsx#TextField"] = { docgenInfo: TextField.__docgenInfo, name: "TextField", path: "packages/asc-ui/src/components/TextField/TextField.tsx#TextField" };
}
catch (__react_docgen_typescript_loader_error) { }